import {React,useEffect,useState} from 'react';
import Particles from 'react-particles-js'

function ParticlesBack(props) {
  const [width,setWidth] = useState(0)
  const [height,setHeight] = useState(0)

  useEffect(()=>{
    window.addEventListener("load",()=>{
      setWidth(`${window.innerWidth}px`)
      setHeight(`${window.innerHeight}px`)
    })
    window.removeEventListener("load",()=>{
      setWidth(`${window.innerWidth}px`)
      setHeight(`${window.innerHeight}px`)
    })
  },[])

  return (
    <Particles
    width={width}
    height={height}
    params={{
      particles: {
        number: {
          value: 75,
          density: {
            enable: true,
            value_area: 800
          }
        },
        color: {
          value: "#ffffff"
        },
        shape: {
          type: "circle",
          stroke: {
            width: 0,
            color: "#000000"
          },
          image: {
            src: "img/github.svg",
            width: 100,
            height: 100
          }
        },
        opacity: {
          value: 0.4,
          random: true,
          anim: {
            enable: true,
            speed: 1,
            opacity_min: 0.1,
            sync: false
          }
        },
        size: {
          value: 3,
          random: true,
          anim: {
            enable: true,
            speed: 2,
            size_min: 0.1,
            sync: false
          }
        },
        line_linked: {
          enable_auto: true,
          distance: 100,
          color: "#fff",
          opacity: 1,
          width: 1,
          condensed_mode: {
            enable: false,
            rotateX: 600,
            rotateY: 600
          }
        },
        move: {
          enable: true,
          speed: 1,
          direction: "none",
          random: false,
          straight: false,
          out_mode: "out",
          bounce: false,
          attract: {
            enable: false,
            rotateX: 600,
            rotateY: 1200
          }
        }
      },
      interactivity: {
        detect_on: "canvas",
        events: {
          onhover: {
            enable: false
          },
          onclick: {
            enable: false
          },
          resize: true
        }
      },
      retina_detect: true
    }}/>
  );
}

export default ParticlesBack;